import React, { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import CircularProgress from '@mui/material/CircularProgress';
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
import MuiLink from '@mui/material/Link';
import Alert from '@mui/material/Alert';
import Container from '@mui/material/Container';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogActions from '@mui/material/DialogActions';
import { Link } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';

function Login() {
  // Load initial state from localStorage for rememberMe and username
  const initialRememberMe = localStorage.getItem('rememberMe') === 'true';
  const initialUsername = initialRememberMe ? (localStorage.getItem('rememberedUsername') || '') : '';
  // sessionStorage
  const initialPassword = sessionStorage.getItem('tempPassword') || '';

  const [username, setUsername] = useState(initialUsername);
  const [password, setPassword] = useState(initialPassword);
  const [rememberMe, setRememberMe] = useState(initialRememberMe);
  const [loading, setLoading] = useState(false);
  const [localError, setLocalError] = useState(''); // Login
  // const navigate = useNavigate(); // 
  // AuthContext
  const { login, isAuthenticated, user, authError, clearAuthError, showDeviceConflictDialog, clearDeviceConflictDialog } = useAuth();

  // sessionStorage
  useEffect(() => {
    if (password) {
      sessionStorage.setItem('tempPassword', password);
    }
  }, [password]);

  // 
  useEffect(() => {
    if (isAuthenticated) {
      sessionStorage.removeItem('tempPassword');
      window.location.replace('/home');
    }
  }, [isAuthenticated]);

  // AuthContextlocalError
  useEffect(() => {
    if (authError) {
      // AuthContext  showDeviceConflictDialog
      if (!(authError.message && authError.message.includes('') && authError.code === 'DEVICE_CONFLICT')) {
        setLocalError(authError.message || '');
      } else {
        setLocalError(''); // localError
      }
    } else {
      // 
      setLocalError('');
    }
  }, [authError]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!username || !password) {
      setLocalError('');
      return;
    }

    setLoading(true);
    setLocalError(''); // 
    clearAuthError(); // AuthContext
    clearDeviceConflictDialog(); // 

    // Save rememberMe state and username if checked
    if (rememberMe) {
      localStorage.setItem('rememberMe', 'true');
      localStorage.setItem('rememberedUsername', username);
    } else {
      localStorage.removeItem('rememberMe');
      localStorage.removeItem('rememberedUsername');
    }

    try {
      await login({ username, password });
    } catch (err) {
      // AuthContext  authError
    } finally {
      setLoading(false);
    }
  };

  const handleForceLogout = async () => {
    clearDeviceConflictDialog(); // 
    setLoading(true);
    clearAuthError(); // 
    setLocalError(''); // 

    try {
      // 
      await login({ username, password, forceLogout: true });
    } catch (forceErr) {
      // AuthContext  authErroruseEffect  localError
    } finally {
      setLoading(false);
    }
  };

  const handleCancelForceLogout = () => {
    clearDeviceConflictDialog(); // 
    clearAuthError(); // 
    setLocalError(''); // 
  };

  return (
    <Container component="main" maxWidth="md" sx={{ mt: 4 }}>
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', p: 3, boxShadow: 3, borderRadius: 2 }}>
        <Typography variant="h5" component="h1" gutterBottom sx={{ mb: 2 }}>
          
        </Typography>

        {localError && (
          <Alert
            severity="error"
            sx={{
              mb: 2,
              backgroundColor: '#ffebee',
              color: '#d32f2f',
              '& .MuiAlert-icon': {
                color: '#d32f2f'
              },
              fontWeight: 'bold',
              fontSize: '1rem',
              py: 1.5,
              border: '1px solid #d32f2f',
              borderRadius: 1,
              width: '100%'
            }}
          >
            {localError}
          </Alert>
        )}

        <form onSubmit={handleSubmit} style={{ width: '100%' }}>
          <TextField
            margin="normal"
            required
            fullWidth
            id="username"
            label=""
            name="username"
            autoComplete="username"
            autoFocus
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            error={!!localError}
            sx={{ mb: 2 }}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            name="password"
            label=""
            type="password"
            id="password"
            autoComplete="current-password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            error={!!localError}
            sx={{ mb: 2 }}
          />
          <FormControlLabel
            control={
              <Checkbox
                value="remember"
                color="primary"
                checked={rememberMe}
                onChange={(e) => setRememberMe(e.target.checked)}
              />
            }
            label=""
            sx={{ mb: 2 }}
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
            disabled={loading}
          >
            {loading ? <CircularProgress size={24} /> : ''}
          </Button>
          <Box sx={{ textAlign: 'center' }}>
            <Link to="/register" style={{ textDecoration: 'none' }}>
              <MuiLink component="span" variant="body2">
                
              </MuiLink>
            </Link>
          </Box>
        </form>
      </Box>

      {/*  -  AuthContext  showDeviceConflictDialog  */}
      <Dialog
        open={showDeviceConflictDialog} //  AuthContext 
        onClose={handleCancelForceLogout}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCancelForceLogout} color="primary">
            
          </Button>
          <Button onClick={handleForceLogout} color="primary" autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
}

export default Login; 