import React from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import { useAuth } from './contexts/AuthContext';
import Layout from './components/Layout'; // Import Layout

// 
import Splash from './pages/Splash';
import Login from './pages/Login';
import Register from './pages/Register';
import Home from './pages/Home';
import Profile from './pages/Profile';
import ProfileEdit from './pages/ProfileEdit';
import MyPosts from './pages/MyPosts';
import Detail from './pages/Detail';
import RechargePaths from './pages/RechargePaths';
import Recharge from './pages/Recharge';
import Withdraw from './pages/Withdraw';
import Notification from './pages/Notification';
import TransactionNotification from './pages/TransactionNotification';
import Settings from './pages/Settings';
import SetPaymentPassword from './pages/SetPaymentPassword';
import CustomerServicePage from './pages/CustomerService';
import OperationResult from './pages/OperationResult';
import NotificationsPage from './pages/Notification';
import WalletPage from './pages/WalletPage';
import InvitedUsers from './pages/InvitedUsers';
import InvitedUserDetail from './pages/InvitedUserDetail';
import ChangePassword from './pages/ChangePassword';
import ForgotPassword from './pages/ForgotPassword';
import ForgotPaymentPassword from './pages/ForgotPaymentPassword';

// 
const ProtectedRoute = ({ children }) => {
  const { isAuthenticated, loading } = useAuth();
  
  if (loading) {
    return null; // 
  }
  
  return isAuthenticated ? <Layout>{children}</Layout> : <Navigate to="/login" replace />;
};

// 
export const routesConfig = [
  { path: '/home', element: <Home />, title: '' },
  { path: '/profile', element: <Profile />, title: '' },
  { path: '/profile/edit', element: <ProfileEdit />, title: '' },
  { path: '/my-posts', element: <MyPosts />, title: '' },
  { path: '/detail/:id', element: <Detail />, title: '' },
  { path: '/recharge-paths', element: <RechargePaths />, title: '' },
  { path: '/recharge-paths/:pathId', element: <Recharge />, title: '' },
  { path: '/withdraw', element: <Withdraw />, title: '' },
  { path: '/notification', element: <Notification />, title: '' },
  { path: '/transaction-notification', element: <TransactionNotification />, title: '' },
  { path: '/settings', element: <Settings />, title: '' },
  { path: '/set-payment-password', element: <SetPaymentPassword />, title: '' },
  { path: '/customer-service', element: <CustomerServicePage />, title: '' },
  { path: '/operation-result', element: <OperationResult />, title: '' },
  { path: '/notifications', element: <NotificationsPage />, title: '' },
  { path: '/wallet', element: <WalletPage />, title: '' },
  { path: '/invited-users', element: <InvitedUsers />, title: '' },
  { path: '/invited-users/:id', element: <InvitedUserDetail />, title: '' },
  { path: '/change-password', element: <ChangePassword />, title: '' },
  { path: '/forgot-password', element: <ForgotPassword />, title: '' },
  { path: '/forgot-payment-password', element: <ForgotPaymentPassword />, title: '' },
];

// 
function AppRoutes() {
  return (
    <Routes>
      {/*  */}
      <Route path="/" element={<Splash />} />
      <Route path="/login" element={<Login />} />
      <Route path="/register" element={<Register />} />
      
      {/*  */}
      {routesConfig.map((route) => (
        <Route 
          key={route.path} 
          path={route.path} 
          element={<ProtectedRoute>{route.element}</ProtectedRoute>} 
        />
      ))}

      {/*  */}
      <Route path="*" element={<Navigate to="/" replace />} />
    </Routes>
  );
}

export default AppRoutes; 