import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  CircularProgress,
  Alert,
  Container,
  Button,
  Grid
} from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { useAuth } from '../contexts/AuthContext';
import apiService from '../services/api';

function InvitedUserDetail() {
  const { id } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [invitedUser, setInvitedUser] = useState(null);
  const [transactions, setTransactions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    // 
    if (!user || user.role !== 'vip') {
      navigate('/home');
      return;
    }

    const fetchUserDetails = async () => {
      try {
        setLoading(true);
        // 
        const userResponse = await apiService.userService.getUserById(id);
        if (!userResponse.success) {
          throw new Error(userResponse.message || '');
        }
        setInvitedUser(userResponse.user);

        // 
        const transactionsResponse = await apiService.transactionService.getUserTransactions(id);
        if (!transactionsResponse.success) {
          throw new Error(transactionsResponse.message || '');
        }
        setTransactions(transactionsResponse.transactions || []);
      } catch (err) {
        setError(err.message || '');
      } finally {
        setLoading(false);
      }
    };

    fetchUserDetails();
  }, [id, user, navigate]);

  // 
  const totalAmount = transactions.reduce((sum, transaction) => {
    return sum + (transaction.type === 'purchase' ? transaction.amount : 0);
  }, 0);

  // VIP
  if (!user || user.role !== 'vip') {
    return null;
  }

  return (
    <Container maxWidth="lg" sx={{ py: 3, px: { xs: 1, sm: 2, md: 3 } }}>
      <Button
        startIcon={<ArrowBackIcon />}
        onClick={() => navigate('/invited-users')}
        sx={{ mb: 3 }}
      >
        
      </Button>

      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
          <CircularProgress />
        </Box>
      ) : error ? (
        <Alert severity="error" sx={{ mt: 2 }}>
          {error}
        </Alert>
      ) : (
        <>
          <Card sx={{ mb: 3 }}>
            <CardContent>
              <Grid container spacing={2}>
                <Grid item xs={12} md={6}>
                  <Typography variant="h6" gutterBottom>
                    
                  </Typography>
                  <Typography variant="body1">
                    : {invitedUser?.username}
                  </Typography>
                  <Typography variant="body1">
                    : {new Date(invitedUser?.createdAt).toLocaleString()}
                  </Typography>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Typography variant="h6" gutterBottom>
                    
                  </Typography>
                  <Typography variant="body1">
                    : {totalAmount.toFixed(2)}
                  </Typography>
                  <Typography variant="body1">
                    : {transactions.length}
                  </Typography>
                </Grid>
              </Grid>
            </CardContent>
          </Card>

          <Typography variant="h6" gutterBottom>
            
          </Typography>
          
          {transactions.length === 0 ? (
            <Alert severity="info" sx={{ mt: 2 }}>
              
            </Alert>
          ) : (
            <TableContainer component={Paper} sx={{ overflowX: 'auto' }}>
              <Table sx={{ minWidth: 400 }}>
                <TableHead>
                  <TableRow>
                    <TableCell></TableCell>
                    <TableCell></TableCell>
                    <TableCell></TableCell>
                    <TableCell></TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {transactions.map((transaction) => (
                    <TableRow key={transaction._id}>
                      <TableCell>
                        
                      </TableCell>
                      <TableCell>{transaction.amount.toFixed(2)}</TableCell>
                      <TableCell>
                        {transaction.status === 'approved' ? '' :
                         transaction.status === 'pending' ? '' :
                         transaction.status === 'rejected' ? '' : ''}
                      </TableCell>
                      <TableCell>
                        {new Date(transaction.createdAt).toLocaleString()}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          )}
        </>
      )}
    </Container>
  );
}

export default InvitedUserDetail; 